	module.exports = {"data":{"markdownRemark":{"html":"<h2>Stakeholders</h2>\n<p>The Stakeholders are the people that will benefit from your project. It is up to the Product Owner to understand the outcome required by the Stakeholders, and to communicate that need to the Delivery Team.\nStakeholders may be made up from any of the following</p>\n<ul>\n<li>Users</li>\n<li>Customers</li>\n<li>Managers</li>\n<li>Funders / investors</li>\n</ul>\n<p>Their participation in the project is essential for success.</p>\n<p>For example, when your team is performing the Sprint Review meetings, the demonstration of the project is for the stakeholders, and their feedback is captured and added to the backlog.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li>Scrum Study <a href='https://www.scrumstudy.com/blog/stakeholders-in-scrum/' target='_blank' rel='nofollow'>Stakeholders in Scrum</a></li>\n<li>Scrum.org <a href='https://www.scrum.org/resources/blog/scrum-who-are-key-stakeholders-should-be-attending-every-sprint-review' target='_blank' rel='nofollow'>Key Stakeholders</a></li>\n<li>Agile Modelling <a href='http://agilemodeling.com/essays/activeStakeholderParticipation.htm' target='_blank' rel='nofollow'>Active Stakeholder Participation</a></li>\n</ul>","fields":{"slug":"/agile/stakeholders/"},"frontmatter":{"title":"Stakeholders"}}},"pathContext":{"slug":"/agile/stakeholders/"}}

